import Vue from 'vue';
import Router from 'vue-router';

import Home from './views/Home.vue';
import Add from './views/Add.vue';
import About from './views/About.vue';

Vue.use(Router);

const rootPath = Vue.config.devtools ? '/' : '/tamflow/';

export default new Router({
  routes: [
    {
      path: rootPath,
      name: 'home',
      component: Home,
    },
    {
      path: rootPath + 'add',
      name: 'add',
      component: Add,
    },
    {
      path: rootPath + 'about',
      name: 'about',
      component: About,
    },
  ],
  linkExactActiveClass: 'is-active',
  mode: 'history',
});
