
















import { Component, Prop, Vue } from 'vue-property-decorator';
import VueGallery from 'vue-gallery';

import PlantTilesItem from '@/components/PlantTilesItem.vue';
import Editor from '@/components/Editor.vue';

import { IPlant, PlaneName } from '@/ts/plant';
import eventBus from '@/ts/event-bus';
import db from '@/ts/db';

@Component({
  components: {
    PlantTilesItem,
    Editor,
    gallery: VueGallery,
  },
})
export default class PlantTiles extends Vue {

  private plants: IPlant[] = [];
  private editing: IPlant | null = null;

  private galleryIndex: (number | null) = null;
  private galleryImages: any[] = [];

  private edit(plant: IPlant) {
    this.editing = plant;
  }

  private update() {
    // console.log('updated');
  }

  private cancelEditing() {
    this.editing = null;
  }

  private sort(plants: IPlant[]) {
    const lang = this.$store.state.language;
    return plants.sort((a, b) => (a.name as PlaneName)[lang] > (b.name as PlaneName)[lang] ? 1 : -1);
  }

  private mounted() {
    db.all().then((plants: IPlant[]) => {
      this.plants = this.sort(plants);
    });
    eventBus.$on('lang', (lang: string) => {
      this.plants = this.sort(this.plants);
    });
  }
}
