type Months = { [k: string]: string };

const months: Months = {
  Март: 'Maaliskuu',
  Апрель: 'Huhtikuu',
  Май: 'Toukokuu',
  Июнь: 'Kesäkuu',
  Июль: 'Heinäkuu',
  Август: 'Elokuu',
  Сентябрь: 'Syyskuu',
};

export default {
  title: {
    ru: 'Цветковые растения города Тампере',
    fi: 'Tampereen kukkivat kasvit',
  },
  gallery: {
    ru: 'Галерея',
    fi: 'Galleria',
  },
  plants: {
    ru: 'Растения',
    fi: 'Kasvit',
  },
  add: {
    ru: 'Добавить',
    fi: 'Lisää',
  },
  about: {
    ru: 'О проекте',
    fi: 'Projektista',
  },
  next: {
    ru: 'Дальше',
    fi: 'Seuraava',
  },
  previous: {
    ru: 'Назад',
    fi: 'Takaisin',
  },
  list: {
    ru: 'Список',
    fi: 'Luettelo',
  },
  tiles: {
    ru: 'Плитка',
    fi: 'Laatta',
  },
  firstFlowers: {
    ru: 'Первое время зацветания',
    fi: 'Ensimmäinen kukitus',
  },

  translate( name: keyof Months) {
    const month = months[name];
    if (month) {
      return month;
    }

    return (name as string).replace(/[^\-]+/g, (item: string) => {
      const tr = months[item];
      return tr ?? item;
    });
  },
};
