import { Component, Watch, Vue } from 'vue-property-decorator';

import PlantList from '@/components/PlantList.vue';
import PlantTiles from '@/components/PlantTiles.vue';

import { Language } from '@/store';
import eventBus from '@/ts/event-bus';

@Component({
  components: {
    PlantList,
    PlantTiles,
  },
})
export default class Home extends Vue {
  private page = 1;
  private pageCount = 1;
  private delayedPage = 0;
  private view = 'list';

  get nearestPageIDs() {
    if (this.pageCount < 3) {
      return [];
    }

    const min = Math.max(2, this.page - 5);
    const max = Math.min(this.pageCount - 1, this.page + 5);

    const result = [];
    for (let i = min; i <= max; i++) {
      result.push(i);
    }

    return result;
  }

  get lang(): Language {
    return this.$store.state.language;
  }

  get title() {
    return this.$store.getters.title;
  }
  get listView() {
    return this.$store.getters.list;
  }
  get tileView() {
    return this.$store.getters.tiles;
  }
  get next() {
    return this.$store.getters.next;
  }
  get previous() {
    return this.$store.getters.previous;
  }

  private goto(page: number) {
    this.page = page;
    window.scrollTo(0, 0);
    window.location.hash = `${page}`;
  }

  private getPageFromHash() {
    let page = 0;

    try {
      page = +window.location.hash.split('#')[1];
    }
    catch (e) {
      return 0;
    }

    if (!Number.isInteger(page)) {
      return 0;
    }

    if (page > 0 && page <= this.pageCount && page !== this.page) {
      this.page = page;
    }

    return page;
  }

  private isViewSelected( view: string ): boolean {
    return this.view === view;
  }

  private setView( view: string ) {
    this.view = view;
  }

  private isLanguageSelected( lang: Language ): boolean {
    return this.lang === lang;
  }

  private setLanguage( lang: Language ) {
    this.$store.commit('setLanguage', lang);
    eventBus.$emit('lang', lang);
  }

  private mounted() {
    this.delayedPage = this.getPageFromHash();

    window.addEventListener('hashchange', e => {
      this.getPageFromHash();
    }, false);
  }

  @Watch('pageCount') private watchPageCount(value: number) {
    if (this.delayedPage) {
      this.page = this.delayedPage;
    }
  }
}
