



























import { Component, Vue } from 'vue-property-decorator';

import db from '@/ts/db';
import eventBus from '@/ts/event-bus';

const rootPath = Vue.config.devtools ? '/' : '/tamflow/';

@Component({})
export default class Navbar extends Vue {

  private isAuthorized = false;

  get plants() {
    return this.$store.getters.plants;
  }
  get add() {
    return this.$store.getters.add;
  }
  get about() {
    return this.$store.getters.about;
  }

  private pathTo(page = '') {
    return rootPath + page;
  }

  private logOut() {
    db.logOut();
  }

  private logIn() {
    eventBus.$emit('show-login');
  }

  private mounted() {
    // set login handlers
    eventBus.$on('login', () => {
      this.isAuthorized = true;
    });
    eventBus.$on('logout', () => {
      this.isAuthorized = false;
    });

    // create burgers
    const $navbarBurgers: HTMLElement[] = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);

    // Check if there are any navbar burgers
    if ($navbarBurgers.length > 0) {

      // Add a click event on each of them
      $navbarBurgers.forEach($el => {
        // Get the target from the "data-target" attribute
        const target = $el.dataset.target;
        if (!target) {
          return;
        }
        const $target = document.getElementById(target);
        if (!$target) {
          return;
        }

        $el.addEventListener('click', () => {
          // Toggle the class on both the "navbar-burger" and the "navbar-menu"
          $el.classList.toggle('is-active');
          $target.classList.toggle('is-active');
        });

        $target.addEventListener('click', () => {
          // Toggle the class on both the "navbar-burger" and the "navbar-menu"
          $el.classList.remove('is-active');
          $target.classList.remove('is-active');
        });
      });
    }
  }
}
