import Vue from 'vue';
import Vuex, { Store } from 'vuex';

import dictionary from './dictionary';

Vue.use(Vuex);

export type Language = 'ru' | 'fi';

export interface IState {
  language: Language;
}

export default new Vuex.Store({
  state(): IState {
    return {
      language: 'ru',
    };
  },
  getters: {
    title(state: IState) {
      return dictionary.title[state.language];
    },
    gallery(state: IState) {
      return dictionary.gallery[state.language];
    },
    plants(state: IState) {
      return dictionary.plants[state.language];
    },
    add(state: IState) {
      return dictionary.add[state.language];
    },
    about(state: IState) {
      return dictionary.about[state.language];
    },
    next(state: IState) {
      return dictionary.next[state.language];
    },
    previous(state: IState) {
      return dictionary.previous[state.language];
    },
    list(state: IState) {
      return dictionary.list[state.language];
    },
    tiles(state: IState) {
      return dictionary.tiles[state.language];
    },
    firstFlowers(state: IState) {
      return dictionary.firstFlowers[state.language];
    },
  },
  mutations: {
    setLanguage(state: IState, lang: Language) {
      state.language = lang;
    },
  },
}) as Store<IState>;
