





















import { Component, Prop, Vue } from 'vue-property-decorator';

import PlantForm from '@/components/PlantForm.vue';

import { Plant, IPlant } from '@/ts/plant';
import db from '@/ts/db';

@Component({
  components: {
    PlantForm,
  },
})
export default class Editor extends Vue {
  @Prop({default: null}) private plant?: IPlant;

  private result = 0;

  private get resultClass() {
    return {
      'is-danger': this.result === -1,
      'is-success': this.result === 1,
      };
  }

  private get resultMessage(): string {
    return this.result > 0 ? 'Готово' : 'Ошибка';
  }

  private get isUpdatable(): boolean {
    return !!this.plant && Plant.isSubmittable(this.plant);
  }

  private update() {
    if (!this.plant) {
      return;
    }

    db.update(this.plant).then( (ref: any, err: any) => {
      this.result = err ? -1 : 1;
      if (this.result === 1) {
        this.$emit('updated');
      }
    });
  }

  private cancel() {
    this.$emit('cancel');
  }
}
