


















import { Component, Vue } from 'vue-property-decorator';

import PlantForm from '@/components/PlantForm.vue';

import { Plant, IPlant } from '@/ts/plant';
import db from '@/ts/db';

@Component({
  components: {
    PlantForm,
  },
})
export default class Add extends Vue {
  private plant: IPlant = new Plant();

  private result = 0;

  get isSubmittable(): boolean {
    return Plant.isSubmittable(this.plant);
  }

  get resultClass() {
    return {
      'is-danger': this.result === -1,
      'is-success': this.result === 1,
      };
  }

  get resultMessage(): string {
    return this.result > 0 ? 'Готово' : 'Ошибка';
  }

  private closeNotification() {
    this.result = 0;
  }

  private add() {
    db.put(this.plant).then( (ref: any, err: any) => {
      this.result = err ? -1 : 1;
      if (this.result > 0) {
        this.reset();
      }
    });
  }

  private reset() {
    this.plant = new Plant();
  }
}
