
































import { Component, Prop, Vue } from 'vue-property-decorator';

import { IPlant } from '@/ts/plant';
import db from '@/ts/db';
import eventBus from '@/ts/event-bus';
import dictionary from '@/dictionary';

const IMAGE_FOLDER = 'https://homepages.tuni.fi/oleg.spakov/tamflow/';

interface GalleryImage {
  title: string;
  description: string;
  href: string;
}

interface Image {
  href: string;
  comment?: string;
}

interface ShowImageArgs {
  images: GalleryImage[];
  index: number;
}

@Component({})
export default class PlantTilesItem extends Vue {
  @Prop() private plant?: IPlant;
  @Prop() private index?: number;

  private isExpanded: boolean = false;
  private galleryIndex: (number | null) = null;

  private isAuthorized = db.isAuthorized;

  get plantStyle(): string {
    return `background-image: url(${this.frontImageURL})`;
  }

  get frontImageURL(): string {
    if (!this.plant) {
      return '';
    }

    return `${IMAGE_FOLDER}${this.plant.imageBase}-front.jpg`;
  }

  get galleryImages(): GalleryImage[] {
    const images: GalleryImage[] = [];
    if (!this.plant) {
      return images;
    }

    for (let i = 0; i < this.plant.imageCount; i++) {
      images.push({
        title: this.plant.name.ru,
        description: '',
        href: `${IMAGE_FOLDER}${this.plant.imageBase}-${i + 1}.jpg`,
      });
    }
    return images;
  }

  get firstFlowers() {
    return this.$store.getters.firstFlowers;
  }

  get mainLanguage() {
    return this.$store.state.language;
  }

  private mainName(plant: IPlant) {
    return (plant.name as any)[ this.$store.state.language ];
  }

  private getPlantSeason(plant: IPlant) {
    if (this.mainLanguage === 'ru') {
      return plant.season;
    }
    else {
      return dictionary.translate(plant.season);
    }
  }

  private setGalleryIndex(index: number) {
    const event: ShowImageArgs = {
      images: this.galleryImages,
      index,
    };
    this.$emit('show-gallery', event);
  }

  private edit() {
    this.$emit('edit', this.plant);
  }

  private mounted() {
    // set login handlers
    eventBus.$on('login', () => {
      this.isAuthorized = true;
    });
    eventBus.$on('logout', () => {
      this.isAuthorized = false;
    });
  }
}
