







































import { Component, Prop, Vue } from 'vue-property-decorator';

import db from '@/ts/db';
import eventBus from '@/ts/event-bus';

enum valueState {
  INVALID = -1,
  UNKNOWN = 0,
  VALID = 1,
}

@Component({})
export default class Login extends Vue {
  private email: string = '';
  private password: string = '';
  private inProgress: boolean = false;
  private showErrorMessage = false;

  private get isEmailValid(): valueState {
    if ( !this.email ) {
      return valueState.UNKNOWN;
    }
    else {
      return /(.{2,})@(\w{2,}\.\w{2,})/.test( this.email ) ? valueState.VALID : valueState.INVALID;
    }
  }

  private get isPasswordValid(): valueState {
    if ( !this.password ) {
      return valueState.UNKNOWN;
    }
    else {
      return !this.password || this.password.length > 5 ? valueState.VALID : valueState.INVALID;
    }
  }

  private get canLogIn(): boolean {
    return !this.inProgress &&
      this.isEmailValid === valueState.VALID &&
      this.isPasswordValid === valueState.VALID;
  }

  private ok( state: valueState ): boolean {
    return state !== valueState.INVALID;
  }

  private login( e: any ) {

    this.inProgress = true;

    db.logIn( this.email, this.password, err => {
      this.inProgress = false;
      if ( err ) {
        this.showErrorMessage = true;
      }
      else {
        this.email = '';
        this.password = '';
        this.showErrorMessage = false;

        eventBus.$emit('hide-login');
      }
    });
  }
}
