import { IPlant } from '@/ts/plant';
import eventBus from '@/ts/event-bus';

const config = {
  apiKey: 'AIzaSyC0TU1Neb_ClQUWh0V3MJ3JBJYKi8fQDus',
  authDomain: 'tamflow-6aa34.firebaseapp.com',
  databaseURL: 'https://tamflow-6aa34.firebaseio.com',
  projectId: 'tamflow-6aa34',
  storageBucket: '',
  messagingSenderId: '863210262585',
};

interface Firebase {
  [x: string]: any;
}

declare var firebase: Firebase;

let user: any = null;
let connected = true;

const app = firebase.initializeApp(config);
const db = app.database().ref();

const auth = firebase.auth();
auth.onAuthStateChanged( onUserChanged );

function onUserChanged( newUser: any ) {

  const loggedIn = !user && !!newUser;
  const loggedOut = !!user && !newUser;

  connected = true;
  user = newUser;

  // eventBus.$emit( 'connected', { user } );

  if ( loggedIn ) {
    eventBus.$emit( 'login' );
  }
  else if ( loggedOut ) {
    eventBus.$emit( 'logout' );
  }
}

class DB {

  public get isAuthorized() {
    return !!user;
  }

  public logIn( email: string, password: string, cb: (err: string | Error | null, user?: any) => void ) {
    if ( !db ) {
      cb( new Error( 'Not connected to the database' ) );
    }

    return auth.signInWithEmailAndPassword( email, password ).then( (loggedUser: any) => {
      cb( null, loggedUser );
    } ).catch( (err: string) => {
      cb( err );
    } );
  }

  public logOut() {
    if ( !!auth ) {
      auth.signOut();
    }
  }

  public put(plant: IPlant) {
    return db.child('plants').push(plant);
  }

  public update(plant: IPlant) {
    const value = { ...plant };
    delete value.id;

    const updates: {[x: string]: any} = {};
    updates[ `plants/${plant.id}` ] = value;

    return db.update( updates );
  }

  public all() {
    return db
      .child('/plants')
      .once('value')
      .then((snapshot: any) => {
        const plantsObj = snapshot.val();
        const plantsArray: IPlant[] = [];
        for (const id in plantsObj) {
          if (!plantsObj.hasOwnProperty(id)) {
            continue;
          }
          const plant = plantsObj[id];
          plant.id = id;
          plantsArray.push(plant);
        }

        return Promise.resolve(plantsArray);
      });
  }
}

const dbSingleton = new DB();

export default dbSingleton;
