export type PlaneName = { [k: string]: string };

export interface IPlant {
  id?: string;
  name: {
    ru: string,
    la: string,
    fi: string,
  };
  season: string;
  description: string;
  imageBase: string;
  imageCount: number;
  refs: {
    la?: string;
    fi?: string;
  };
}

export class Plant implements IPlant {

  public static isSubmittable(value: IPlant): boolean {
    return value.name.ru.length > 0 &&
      value.name.la.length > 0 &&
      value.season.length > 0 &&
      value.imageBase.length > 0 &&
      value.imageCount > 0;
  }

  public name = {
    ru: '',
    la: '',
    fi: '',
  };
  public season = '';
  public description = '';
  public imageBase = '';
  public imageCount = 5;
  public refs = {
    la: '',
    fi: '',
  };
}
